.messageFooter {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  white-space: nowrap;
}
.messageFooter.onlyMedia {
  bottom: 5px;
  left: 0;
  margin-left: 10px;
  position: absolute;
  right: 5px;
}

.startWebxdcButton {
  font-size: 14px;
  margin: 10px 0;
  padding-bottom: 6px;
  padding-left: 13px;
  padding-right: 13px;
  padding-top: 7px;
}

.vcard {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  text-align: inherit;
  display: flex;
  background-color: rgba(0, 0, 0, 0.1333333333);
  border-radius: 10px;
  padding: 9px;
  padding-top: 0;
  padding-right: 15px;
}
.vcard .avatar {
  margin-bottom: 0;
}
.vcard .contactInfo {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  padding-left: 10px;
}
.vcard .displayName {
  margin-bottom: 0;
  margin-top: 10px;
  font-weight: 200;
  font-size: medium;
}