@charset "UTF-8";
.reactions {
  cursor: pointer;
  margin-right: 10px;
  position: relative;
  line-height: 1;
  top: 16px;
}

.emoji {
  background-color: var(--messageIncomingBg);
  border-radius: 24px;
  border: solid 1px rgba(100, 100, 100, 0.3);
  font-size: 16px;
  padding: 2px 6px;
}
.emoji ~ .emoji {
  margin-left: 2px;
}
.emoji.isFromSelf {
  background-color: var(--messageOutgoingBg);
}
.emoji.showMore {
  color: var(--messageText);
}
.emoji.showMore::after {
  content: "⋯";
  display: inline-block;
  position: relative;
  top: 1px;
}

.emojiCount {
  bottom: 1px;
  color: var(--messageText);
  font-size: 12px;
  margin-left: 3px;
  margin-right: 1px;
  position: relative;
}

.openReactionsListDialogButton {
  position: absolute;
  inset: 0;
  border: none;
  background: transparent;
}