.microphoneButton {
  height: 40px;
  width: 40px;
  padding-left: 6px;
  padding-bottom: 8px;
  background-color: var(--composerBg);
  border: none;
}
.microphoneButton span {
  display: block;
  width: 25px;
  height: 25px;
  margin-top: 7.5px;
  -webkit-mask-image: url(./images/microphone.svg);
  -webkit-mask-size: contain;
  background-color: var(--composerPlaceholderText);
}
.microphoneButton span:hover {
  background-color: var(--buttonSecondaryText);
}

.audioRecorder {
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: stretch;
  flex-grow: 1;
  margin-left: 10px;
}
.audioRecorder p {
  margin: 12px 0 0 3px;
}
.audioRecorder .microphoneButton span {
  animation: pulse 1.5s infinite ease-in-out;
}
@keyframes pulse {
  0% {
    background-color: rgba(235, 76, 76, 0.3450980392);
  }
  50% {
    background-color: #d70b0b;
  }
  100% {
    background-color: rgba(235, 76, 76, 0.3450980392);
  }
}
.audioRecorder button:not(.microphoneButton) {
  padding: 4px;
  height: 32px;
  margin-top: 4px;
  width: 85px;
  border-radius: 5px;
  border: none;
  margin-left: 16px;
}
.audioRecorder button:not(.microphoneButton).stopRecording {
  background-color: var(--buttonPrimaryBackground);
  color: #fff;
  margin-right: 40px;
}
.audioRecorder button:not(.microphoneButton).stopRecording:hover {
  background-color: var(--buttonPrimaryHover);
}
.audioRecorder button:not(.microphoneButton).cancel {
  background-color: var(--buttonSecondaryBackground);
  color: var(--buttonSecondaryText);
  border: 1px solid #ccc;
}
.audioRecorder button:not(.microphoneButton).cancel:hover {
  background-color: var(--buttonSecondaryHover);
}

.timer {
  margin-top: 12px;
}

.volumeBarContainer {
  flex: 1;
  padding-right: 10px;
}
.volumeBarContainer .volumeBar {
  display: flex;
  position: relative;
  display: flex;
  margin: 15px 20px 0 15px;
  height: 12px;
  width: 100%;
  max-width: 250px;
  min-width: 60px;
}
.volumeBarContainer .volumeBar .mask {
  display: flex;
  width: 100%;
  z-index: 10;
  height: 12px;
  position: absolute;
  left: 0;
}
.volumeBarContainer .volumeBar .mask .step {
  display: flex;
  width: 10%;
  height: 10px;
  border-left: 1px solid #bbb;
  border-top: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
}
.volumeBarContainer .volumeBar .mask .step:last-child {
  border-right: 1px solid #bbb;
}
.volumeBarContainer .volumeBar .level {
  width: 100%;
  background-color: var(--composerBg);
  height: 10px;
  position: absolute;
  right: 0;
  z-index: 5;
}
.volumeBarContainer .volumeBar .colorBackground {
  width: 100%;
  background: none;
  background: linear-gradient(to right, rgb(52, 237, 52), rgb(52, 237, 52) 50%, orange 75%, red 90%);
  height: 10px;
  position: absolute;
}

@media (max-width: 400px) {
  .volumeBarContainer .volumeBar {
    display: none;
  }
}