:global(.main-screen.small-screen.chat-view-open) .chatListAndNavbar {
  display: none;
}

:global(.main-screen.small-screen:not(.chat-view-open)) .chatAndNavbar {
  display: none;
}

.chatListAndNavbar {
  display: flex;
  flex-direction: column;
  width: 0;
  flex-grow: 3;
  min-width: 295px;
}
.chatListAndNavbar .chatListNavbar {
  height: var(--navBarHeight);
  background-color: var(--navBarBackground);
  color: var(--navBarText);
  border-bottom: var(--outlineProperties);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  padding: 0px 10px;
}
.chatListAndNavbar .chatListNavbar .archivedChatsTitle {
  flex: 1 1 auto;
  overflow-x: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 18px;
}

.chatAndNavbar {
  width: 0;
  flex-grow: 8;
  display: flex;
  flex-direction: column;
}
.chatAndNavbar .chatNavbar {
  height: var(--navBarHeight);
  background-color: var(--navBarBackground);
  color: var(--navBarText);
  border-bottom: var(--outlineProperties);
  flex-shrink: 0;
  display: flex;
  align-items: center;
  padding: 0px;
}
.chatAndNavbar .chatNavbar .chatNavbarHeadingWrapper {
  width: 0;
  flex-grow: 1;
}
.chatAndNavbar .chatNavbar :global(.navbar-heading--button) {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  text-align: inherit;
}
.chatAndNavbar .chatNavbar :global(.navbar-heading) {
  max-width: 100%;
  display: flex;
  align-items: center;
  padding-left: 5px;
  margin-right: 0px;
  overflow-x: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 18px;
  font-weight: bold;
  overflow: hidden;
}
.chatAndNavbar .chatNavbar :global(.views) {
  margin-right: 15px;
  display: flex;
  height: 100%;
}
.chatAndNavbar .chatNavbar :global(.navbar-button) {
  padding: 0;
  width: 40px;
  border-radius: 0;
}
.chatAndNavbar .chatNavbar :global(.navbar-button):hover {
  background-color: var(--navBarButtonHover);
}
.chatAndNavbar .chatNavbar :global(#three-dot-menu-button) {
  height: 40px;
  border-radius: 100%;
}