.icon {
  background-color: var(--colorPrimary);
  display: inline-block;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: contain;
}
.icon.navbar {
  background-color: var(--navBarText);
}
.icon.settings {
  background-color: var(--settingsIconColor);
}
.icon.contextMenu {
  background-color: var(--contextMenuText);
}
.icon.fullscreenControls {
  background-color: var(--fullScreenMediaButtons);
}
.icon.iconColorCSSVar {
  background-color: var(--icon-color);
}
.icon.remove:hover {
  background-color: #bf0000;
}

.iconButton {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  text-align: inherit;
  display: flex;
}