.settingsHeading {
  color: var(--globalText);
  font-size: 16px;
  margin-bottom: 10px;
  margin-left: 0;
  margin-right: 0;
  margin-top: 10px;
  padding-left: 30px;
  padding-right: 30px;
}

.settingsSeparator {
  background-color: rgba(0, 0, 0, 0.05);
  border: 0;
  height: 5px;
  margin-bottom: 15px;
  margin-left: 0;
  margin-right: 0;
  margin-top: 15px;
}

.settingsButton {
  background-color: transparent;
  border: 0;
  color: var(--globalText);
  min-height: 40px;
  padding-left: 30px;
  padding-right: 30px;
  text-align: left;
  width: 100%;
}
.settingsButton:hover {
  background-color: var(--chatListItemBgHover);
  cursor: pointer;
}
.settingsButton.highlight {
  color: var(--colorPrimary);
  font-weight: bold;
}

.settingsIconButton {
  align-items: center;
  background-color: unset;
  border: none;
  cursor: pointer;
  display: flex;
  height: 40px;
  padding-bottom: 0;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 0;
  width: 100%;
}
.settingsIconButton:hover {
  background-color: var(--chatListItemBgHover);
}

.settingsIconButtonLabel {
  color: var(--globalText);
  flex-grow: 1;
  line-height: 2;
  margin-left: 20px;
  text-align: start;
}

.settingsIcon {
  background-color: var(--settingsIconColor);
}

.settingsSelector {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  text-align: inherit;
  width: 100%;
  align-items: center;
  cursor: pointer;
  display: flex;
  height: 40px;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
  user-select: none;
}
.settingsSelector:hover {
  background-color: var(--chatListItemBgHover);
}

.settingsSelectorLabel {
  color: var(--globalText);
}

.settingsSelectorValue {
  color: var(--colorPrimary);
  font-weight: lighter;
  overflow: hidden;
  text-overflow: ellipsis;
}

.settingsSwitch {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  min-height: 40px;
  padding-bottom: 10px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  user-select: none;
}
.settingsSwitch:hover {
  background-color: var(--chatListItemBgHover);
}

.settingsSwitchLeft {
  display: flex;
  flex-direction: column;
  margin-right: 15px;
}

.settingsSwitchLabel {
  color: var(--globalText);
}
.settingsSwitchLabel.disabled {
  color: rgba(92, 112, 128, 0.6);
}

.settingsSwitchDescription {
  color: var(--chat-list-item-archived-label-border);
  font-size: smaller;
  margin-top: 3px;
}
.settingsSwitchDescription.disabled {
  color: rgba(92, 112, 128, 0.6);
}

.settingsSwitchRight {
  align-items: center;
  display: flex;
}

.profile {
  display: flex;
  margin-bottom: 20px;
  margin-left: 25px;
  margin-right: 30px;
}

.profileDetails {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
  overflow: hidden;
}

.profileDisplayName {
  font-size: 18px;
  font-weight: 200;
}

.profileBio {
  margin-top: 5px;
  overflow: hidden;
  text-wrap-mode: nowrap;
  text-overflow: ellipsis;
}