.searchInput {
  background-color: var(--searchInputBackgroundColor);
  border-radius: 10px;
  border: 0;
  caret-color: var(--navBarText);
  color: var(--navBarText);
  flex: 1;
  font-size: 16px;
  height: 30px;
  margin-right: 5px;
  padding-bottom: 0;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 0;
  width: 100%;
  outline: none;
}
.searchInput::placeholder {
  color: var(--navBarSearchPlaceholder);
}

.searchInputButton {
  align-items: center;
  background-color: transparent;
  color: var(--navBarText);
  border-radius: 10px;
  border: 0;
  display: flex;
  height: 30px;
  justify-content: center;
  padding-bottom: 0;
  padding-left: 1px;
  padding-right: 0;
  padding-top: 0;
  min-width: 30px;
}
.searchInputButton:hover {
  background-color: var(--navBarButtonHover);
}

.searchInputButtonIcon {
  background-color: currentcolor;
}