.viewProfileDialog {
  height: 100%;
}

.viewProfileDialogBody {
  display: flex;
  flex-direction: column;
  padding-bottom: 2px;
}

.statusText {
  padding: 15px 0;
  color: var(--globalText);
  white-space: pre-wrap;
}

.contactAttributes {
  margin-top: 15px;
  margin-inline-start: -4px;
  font-size: 1.08em;
}
.contactAttributes > * {
  margin-bottom: 8px;
}
.contactAttributes > * > img {
  width: 1.15em;
  height: 1.15em;
  margin-inline-start: 0.3em;
  margin-inline-end: 0.3em;
  vertical-align: middle;
}

.verification {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  text-align: inherit;
}

.buttonWrap {
  margin: 10px 0 24px 30px;
}
.buttonWrap button {
  min-width: 150px;
}

.extendedSeparator {
  margin-left: -30px;
  margin-right: -30px;
  padding: 2px;
}

.mutualChats {
  margin-inline-start: 22px;
  margin-inline-end: 20px;
  margin-top: 8px;
  flex-grow: 1;
}
.mutualChats .chat-list-item {
  padding: 0px 20px;
}