.largeProfileImage {
  flex: 0 0 100px;
  height: 100px;
  user-select: none;
  width: 100px;
}

.largeProfileImageArea {
  align-items: center;
  --local-avatar-color: #bcbac9;
  background-color: var(--local-avatar-color);
  border-radius: 100%;
  color: white;
  display: flex;
  font-size: 64px;
  height: 100px;
  justify-content: center;
  object-fit: cover;
  object-position: center;
  pointer-events: none;
  width: 100px;
}
@supports (text-wrap: wrap) {
  .largeProfileImageArea {
    background: linear-gradient(var(--avatarGradientAngle), var(--local-avatar-color), color-mix(in srgb, var(--local-avatar-color) var(--avatarGradientMixFactor), white));
  }
}

.largeProfileImageDefaultIcon {
  background-color: white;
}