:root {
  /* 'EmojiMart' represents the general emoji font of the app, themes may redefine it */
  --emojifonts: "EmojiMart", "Apple Color Emoji", NotoEmoji;
  --fonts-default: Roboto, var(--emojifonts), "Helvetica Neue", Arial, Helvetica,
    NotoMono, sans-serif;
  --fonts-system: system-ui, var(--fonts-default);
}

/* Theming Colors */
.dialog {
  padding: 0;
}
@media (width < 500px) {
  .dialog {
    max-width: calc(100vw - 1rem) !important;
    min-width: 0 !important;
  }
}
.dialog.unstyled {
  background: none;
  width: 100vw;
  height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  border: none;
  margin: 0;
}
.dialog:not(.unstyled) {
  background-color: var(--bp4DialogBgPrimary);
  color: var(--globalText);
  border-radius: 10px;
  border: none;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 4px 8px rgba(16, 22, 26, 0.2), 0 0 20px 3px rgba(16, 22, 26, 0.2);
  flex-direction: column;
  max-height: calc(100% - 50px);
  max-width: 733px;
  min-width: 350px;
  width: unset;
  justify-content: center;
}
.dialog::backdrop {
  background-color: var(--dialogBackdropBackground);
}

.dialogHeader {
  width: 100%;
  align-items: center;
  background-color: var(--bp4DialogHeaderBg);
  border-bottom: var(--outlineProperties);
  display: flex;
  padding-bottom: 20px;
  padding-left: 30px;
  padding-right: 25px;
  padding-top: 20px;
  user-select: none;
}

.dialogHeading {
  align-items: center;
  color: var(--bp4Heading);
  display: flex;
  flex: 1 1 auto;
  font-size: 18px;
  font-weight: 600;
  height: 30px;
  margin: 0;
}

.dialogBody {
  width: 100%;
  background-color: var(--bp4DialogBgPrimary);
  flex: 1 1 auto;
  max-height: 90vh;
  overflow: overlay;
}
.dialogBody::-webkit-scrollbar-track {
  background: transparent;
}

.dialogContent {
  padding-left: 30px;
  padding-right: 30px;
  line-height: 22px;
}
.dialogContent.paddingTop {
  padding-top: 20px;
}
.dialogContent.paddingBottom {
  padding-bottom: 20px;
}

.dialogBody:last-child {
  margin-bottom: 20px;
}

.dialogFooter {
  width: 100%;
  background-color: var(--bp4DialogBgPrimary);
  padding-bottom: 20px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 20px;
}

.headerButton {
  align-items: center;
  background-color: transparent;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  display: flex;
  height: 30px;
  justify-content: center;
  padding: 0;
  width: 30px;
}
.headerButton + .headerButton {
  margin-left: 5px;
}
.headerButton:hover {
  background-color: var(--chatListItemBgHover);
}

.headerButtonIcon {
  background-color: var(--bp4DialogHeaderIcon);
  margin: 0 !important;
}

.headerThreeDotButton {
  margin: 0 !important;
  padding: 0 !important;
}

.backButton {
  margin-right: 20px;
}

.footerActions {
  display: flex;
}
@media (width <= 500px) {
  .footerActions {
    flex-direction: column;
  }
}
.footerActions.spaceBetween {
  justify-content: space-between;
}
.footerActions.start {
  justify-content: flex-start;
}
.footerActions.end {
  justify-content: flex-end;
}
.footerActions.center {
  justify-content: center;
}

.footerActionButton {
  min-width: 100px;
  padding: 8px 12px !important;
}
.footerActionButton + .footerActionButton {
  margin-left: 20px;
}
@media (width <= 500px) {
  .footerActionButton + .footerActionButton {
    margin-left: 0px;
    margin-top: 20px;
  }
}